import React, {propTypes,ReactDom} from "external/react";
import {LoggerMixin} from "external/util";
import {ViewTestForm} from "external/viewtest";

const testForm = React.createClass({

    getInitialState(){
        return {
            "address1": "sdfsdf",
            "addressLabel":"label1"


        };
    },

    render(){
        return (
<div>
            <ViewTestForm
                address1={this.state.address1}
                addressLabel={this.state.addressLabel}
                buttonText="submit"
            ></ViewTestForm>
    </div>
        );
    }

});
export default testForm;





